// import React from 'react';
import { useState, useEffect } from 'react';
import { useParams } from "react-router-dom"
import fileDownload from 'js-file-download';
import { sentenceCase } from 'change-case';
import FileSaver, { saveAs } from 'file-saver';
import {
    MDBCol,
    MDBContainer,
    MDBRow,
    MDBCard,
    MDBCardText,
    MDBCardBody,
    MDBCardImage,
    MDBBtn,
    MDBBreadcrumb,
    MDBBreadcrumbItem,
    MDBProgress,
    MDBProgressBar,
    MDBIcon,
    MDBListGroup,
    MDBListGroupItem,
    MDBTable,
    MDBTableHead,
    MDBTableBody
} from 'mdb-react-ui-kit';
import {
    Button,
    Switch,
    InputLabel,
    MenuItem,
    FormControl,
    Select,
    TextField,
    autocompleteClasses,
    Container,
    Stack,
    Grid,
    Typography
} from "@mui/material"
import { useForm } from "react-hook-form";
import Box from '@mui/material/Box';
import SendIcon from '@mui/icons-material/Send';
import CloudUploadIcon from '@mui/icons-material/CloudUpload';
import AddCircleOutlineIcon from '@mui/icons-material/AddCircleOutline';

import { fontWeight } from '@mui/system';
import axios from 'axios';
import Popup from '../../components/Popup';

import Label from '../../components/label';
import NewUpload from './NewUpload';
import { variables } from "../../variables";

import Notification from '../../components/Notifications';
import NewRecommendation from './NewRecommendation';
import Caupdate from './Caupdate';
import { useAuthContext } from '../../auth/useAuthContext';




export default function InvestmentsView(props) {
    const { user } = useAuthContext();
    const params = useParams()
    const userId = params.userId

    const [values, setValues] = useState([]);
    const [option, setOption] = useState('');
    const [comments, setComments] = useState([]);
    const [value, setValue] = useState('')
    const [row, setRows] = useState({});
    const [file, setFiles] = useState('')
    const [uploaddocuments, SetUploadDocuments] = useState(null)
    const [openUpload, setUpload] = useState(false)
    const [openRecommend, setRecommend] = useState(false)
    const [recommendation, SetRecommendation] = useState(null)
    const [recommended, setRecommended] = useState([])
    const [openPopup, setOpenPopup] = useState(false)
    const [comment, setComment] = useState(null)
    const { register, handleSubmit, formState: { errors } } = useForm({
        mode: "onBlur"
    });
    const [notify, setNotify] = useState({ isOpen: false, message: '', type: '' })

    const registerOptions = {
        status: '',
    }
    // const [Titles, setTitles] = useState([]);
  
    const openUploadForm = row => {
        SetUploadDocuments(row)
        setUpload(true)

    }

    const getUploads = async () => {
        try {
            const Endpoint = `Upload/${userId}`
            const data = await axios.get(variables.API_URL + Endpoint);

            setValues(data.data)

        }
        catch (e) {
            console.log(e);
        }
    }

    const getApp = async () => {
        try {
            const Endpoint = `Investments/${userId}`
            const data = await axios.get(variables.API_URL + Endpoint);

            setRows(data.data)

        }
        catch (e) {
            console.log(e);
        }
    }

    const getFile = async (row) => {
        const Endpoint = `Upload/download/${row.id}`
        axios({
           
            url: variables.API_URL+Endpoint, // your url
            method: 'POST',
            responseType: 'blob', // important
        }).then((res) => {
            FileSaver.saveAs(res.data, row.filepath)
        });
    }

    const getrecommended = async () => {
        try {
            const Endpoint = `Voting/${userId}`
            const data = await axios.get(variables.API_URL + Endpoint);

            setRecommended(data.data)

        }
        catch (e) {
            console.log(e);
        }
    }

    const getComments = async () => {
        try {
            const Endpoint = `Recommend/${userId}`
            const data = await axios.get(variables.API_URL + Endpoint);

            setComments(data.data)

        }
        catch (e) {
            console.log(e);
        }
    }

    const openRecommendForm = row => {
        SetRecommendation(row)
        setRecommend(true)

    }




    useEffect(() => {
        
        getApp();
        getrecommended();
        getUploads();
        getComments();
       
    }, []);



    const insernewdata = (values) => {

        const Endpoint = 'Recommend'
        axios.post(variables.API_URL + Endpoint, {
            recommendation: values.recommendation,
            role: user.role,
            application: userId


        })
            .then((response) => {

                setNotify({
                    isOpen: true,
                    message: 'Recommendation Successfuly Submited',
                    type: 'success'
                }

                )

                setRecommend(false)

                const Newcomment = response.data;

                setComments(oldrows => [...oldrows, Newcomment])

            })
            .catch((e) => {
                console.log("Hello errors")
                setNotify({
                    isOpen: true,
                    message: 'Employee not hired',
                    type: 'error'
                }
                )
            })
    }


    function handleError(event) { }
    const label = { inputProps: { 'aria-label': 'Switch demo' } };
    return (
        <>
            <section style={{ backgroundColor: '#fafafa' }}>
                <MDBContainer className="py-5">

                    <MDBRow>
                        <MDBCol lg="8">
                            <MDBCard className="mb-1">
                                <MDBCardBody className="text-center">

                                    <MDBTable bordered borderColor="primary">
                                        <MDBTableHead>
                                            <tr>
                                                <td colSpan={2}>
                                                    <Box sx={{ fontWeight: 'bold', m: 1 }}>Investment  Details</Box>
                                                </td>
                                            </tr>

                                        </MDBTableHead>

                                        <MDBTableBody>
                                            <tr>
                                                <th style={{ fontWeight: "bold", textAlign: "left" }}>Applicant Name:</th>
                                                <td>{row.applicantName}</td>

                                            </tr>

                                            <tr>
                                                <th style={{ fontWeight: "bold", textAlign: "left" }}>Account:</th>
                                                <td>{row.account}</td>

                                            </tr>
                                            <tr>
                                                <th style={{ fontWeight: "bold", textAlign: "left" }}>Address:</th>
                                                <td>{row.address}</td>

                                            </tr>
                                            <tr>
                                                <th style={{ fontWeight: "bold", textAlign: "left" }}>Post Code:</th>
                                                <td>{row.postcode}</td>

                                            </tr>
                                            <tr>
                                                <th style={{ fontWeight: "bold", textAlign: "left" }}>Mobile:</th>
                                                <td>{row.mobile}</td>

                                            </tr>
                                            <tr>
                                                <th style={{ fontWeight: "bold", textAlign: "left" }}>Telephone:</th>
                                                <td>{row.teloffice}</td>

                                            </tr>

                                            <tr>
                                                <th style={{ fontWeight: "bold", textAlign: "left" }}>Nature of Business:</th>
                                                <td>{row.natureofbusiness}</td>

                                            </tr>


                                            <tr>
                                                <th style={{ fontWeight: "bold", textAlign: "left" }}>Purpose:</th>
                                                <td>{row.purpose}</td>

                                            </tr>

                                        </MDBTableBody>
                                    </MDBTable>

                                </MDBCardBody>
                            </MDBCard>


                        </MDBCol>
                        <MDBCol lg="8">
                            <MDBCard className="mb-4">
                                <MDBCardBody>
                                    <MDBTable bordered borderColor="primary">
                                        <MDBTableHead>
                                            <tr>
                                                <td colSpan={7}>
                                                    <Box sx={{ fontWeight: 'bold', m: 1 }}>Financing Details</Box>
                                                </td>
                                            </tr>

                                        </MDBTableHead>

                                        <MDBTableBody>
                                            <tr>
                                                
                                                <th scope='col'>Amount</th>
                                                <th scope='col'>Contribution</th>
                                                <th scope='col'>Facilty Period</th>
                                            </tr>
                                            <tr>
                                                <th scope='row'>{row.amount}</th>
                                                <td>{row.contribution}</td>
                                                <td>{row.period}</td>

                                            </tr>


                                        </MDBTableBody>
                                    </MDBTable>

                                    <MDBTable bordered borderColor="primary" >
                                        <MDBTableHead>
                                            <tr>
                                                <td colSpan={7}>
                                                    <Box sx={{ fontWeight: 'bold', m: 1 }}>Security Details</Box>
                                                </td>
                                            </tr>
                                        </MDBTableHead>
                                        <MDBTableBody>
                                            <tr>
                                                <th scope='col'>Type of Security</th>
                                                <th scope='col'>Name of guarantor</th>
                                                <th scope='col'>Nature of business</th>
                                                <th scope='col'>Address</th>
                                                <th scope='col'>Phone</th>
                                            </tr>
                                            <tr>
                                                <th scope='row'>{row.security}</th>
                                                <td>{row.nameguarantor}</td>
                                                <td>{row.naturesecurity}</td>
                                                <td>{row.addresssecurity}</td>
                                                <td>{row.phone}</td>
                                            </tr>
                                        </MDBTableBody>
                                    </MDBTable>



                                </MDBCardBody>
                            </MDBCard>


                            <MDBRow>
                                <MDBCol md="6">
                                    <MDBCard className="mb-4 mb-md-0">
                                        <MDBCardBody>
                                            <MDBCardText className="mb-4"><span className="text-primary font-italic me-1">Comments and Recommendations</span>

                                                <Button variant="outlined" onClick={() => openRecommendForm(row)} startIcon={<AddCircleOutlineIcon />}>
                                                    Add
                                                </Button></MDBCardText>


                                            {comments.map(row => (

                                                <MDBCardText className="mb-1" style={{ fontSize: '.77rem' }}>{row.recommendation}  <span className="text-primary font-italic me-1">Recommended by: {row.role}</span></MDBCardText>
                                            ))}


                                        </MDBCardBody>
                                    </MDBCard>
                                </MDBCol>

                                <MDBCol md="6">
                                    <MDBCard className="mb-4 mb-md-0">
                                        <MDBCardBody>
                                            <MDBCardText className="mb-4"><span className="text-primary font-italic me-1">Attached Documments</span><Button onClick={() => openUploadForm(row)}  variant="outlined" startIcon={<CloudUploadIcon />}>
                                                Upload
                                            </Button> </MDBCardText>
                                            {values.map(row => (

                                                <li key={row.id}>

                                                    <Button onClick={() => getFile(row)}>{row.fileName}</Button>
                                                </li>
                                            ))}

                                        </MDBCardBody>
                                    </MDBCard>
                                </MDBCol>
                            </MDBRow>

                            <br />

                            <MDBCard className="mb-4 mb-md-0">
                                <MDBCardBody>
                                  
                                    {user?.role === "COM" && (
                                        <MDBTable bordered borderColor="primary" >
                                            <MDBTableHead>

                                                <tr>
                                                    <td colSpan={7}>
                                                        <Box sx={{ fontWeight: 'bold', m: 1 }}> Credit Commitee Decisions  </Box><span className="text-primary font-italic me-1">Current Status</span>{row.status}
                                                    </td>
                                                </tr>

                                            </MDBTableHead>
                                            <MDBTableBody>
                                                {recommended.map((item, i) => (
                                                    <tr key={i}>
                                                        <th>{item.fullname}</th>
                                                        <td><Label color={(item.status === 'Approved' && 'success') || (item.status === 'Declined' && 'error')}>{sentenceCase(item.status)}</Label></td>
                                                    </tr>



                                                ))}
                                            </MDBTableBody>
                                        </MDBTable>
                                    )
                                    }

                                    {user?.role === "CA" && (
                                        <MDBTable bordered borderColor="primary" >
                                            <MDBTableHead>

                                                <tr>
                                                    <td colSpan={7}>
                                                        <Box sx={{ fontWeight: 'bold', m: 1 }}> Credit Commitee Decisions  </Box><span className="text-primary font-italic me-1">Current Status</span>{row.status}
                                                    </td>
                                                </tr>

                                            </MDBTableHead>
                                            <MDBTableBody>
                                                {recommended.map((item, i) => (
                                                    <tr key={i}>
                                                        <th>{item.fullname}</th>
                                                        <td><Label color={(item.status === 'Approved' && 'success') || (item.status === 'Declined' && 'error')}>{sentenceCase(item.status)}</Label></td>
                                                    </tr>



                                                ))}
                                            </MDBTableBody>
                                        </MDBTable>
                                    )
                                    }
                                    {user?.role === "RM" && (
                                        <MDBTable bordered borderColor="primary" >
                                            <MDBTableHead>

                                                <tr>
                                                    <td colSpan={7}>
                                                        <Box sx={{ fontWeight: 'bold', m: 1 }}> Credit Commitee Decisions  </Box><span className="text-primary font-italic me-1">Current Status</span>{row.status}
                                                    </td>
                                                </tr>

                                            </MDBTableHead>
                                            <MDBTableBody>
                                                {recommended.map((item, i) => (
                                                    <tr key={i}>
                                                        <th>{item.fullname}</th>
                                                        <td><Label color={(item.status === 'Approved' && 'success') || (item.status === 'Declined' && 'error')}>{sentenceCase(item.status)}</Label></td>
                                                    </tr>



                                                ))}
                                            </MDBTableBody>
                                        </MDBTable>
                                    )
                                    }
                                </MDBCardBody>
                            </MDBCard>
                        </MDBCol>
                    </MDBRow>
                </MDBContainer>
            </section>


            <Popup
                openPopup={openUpload}
                setUpload={setUpload}
                title="New Upload"
            >
                <NewUpload
                    uploaddocuments={uploaddocuments}
                    setUpload={setUpload}
                />


            </Popup>


            <Popup
                openPopup={openRecommend}
                setRecommend={setRecommend}
                title="Recommendation / Comment"
            >
                <NewRecommendation
                    recommendation={recommendation}
                    setRecommend={setRecommend}
                     insernewdata={insernewdata}
                />


            </Popup>

            <Popup
                openPopup={openPopup}
                setOpenPopup={setOpenPopup}
                title="Recommendation Credit Analysist"
            >


                <Caupdate
                    setOpenPopup={setOpenPopup}
                   // insernewdata={updatecaStatus}
                />


            </Popup>



            <Notification
                notify={notify}
                setNotify={setNotify}
            />
        </>
    );
}